


import React from 'react'
import { NavLink } from 'react-router-dom'





const Common = (Props) => {
    return (
        <>
            <section id="header" className="d-flex align-items-center">
                <div className="container-fluid">
                    <div className="row home-page">
                        <div className="col-10 mx-auto ">
                            <div className="row">



                                <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column home-desc">
                                    <h1>
                                       {Props.name}<strong className="brand-name">TheWebPlanet</strong>
                                    </h1>
                                    <h2 className="my-3">
                                        We are the team of talented developer making websites.

                                    </h2>
                                    <div className="mt-3">
                                        <NavLink to={Props.visit} className="btn-get-started"> {Props.btname}</NavLink>

                                    </div>

                                </div>

                                <div className="col-lg-6 order-1 order-lg-2 header-img">
                                    <img src={Props.imgsrc} alt="Common-img" className='img-fluid animated' width="96%" />
                                    
                                </div>
                        
                            </div>
                        </div>

                    </div>

                </div>


            </section>
        </>
    )
}


export default Common;