



import React from 'react'
import myimg from '../src/images/bg1.gif'
import Common from './Common'




const Home = () => {
    return (
        <>
            <Common 
                name = 'Grow Your Business with '
                imgsrc = {myimg}
                visit = '/service'
                btname = 'Get Started'
            />
        </>
    )
}


export default Home;