





import React from 'react'
import myaboutimg from '../src/images/new2.gif'
import Common from './Common'



const About = () => {
    return (
        <>
            <Common 
                name = 'Welcome to About Page '
                imgsrc = {myaboutimg}
                visit = '/contact'
                btname = 'Contact Now'
            />
        </>
    )
}


export default About;