




import React, { useState } from 'react'
import { NavLink } from 'react-router-dom'
import email from '../src/images/visitimg/email.gif'
import fb from '../src/images/visitimg/fb.gif'
import insta from '../src/images/visitimg/insta.gif'



const Contact = () => {


    const [data, setData] = useState({
        fullname : "",
        phone : "",
        email : "",
        message : ""
    })


    const InputEvent = (event) =>{

        const {value , name} = event.target;
        setData((preValue) =>{
            return{
                ...preValue ,
                [name] : value
            }

        })

        


    }
    

    const formSubmit = (e) =>{
        e.preventDefault();
        alert(`Your fullName is ${data.fullname} and your phone number is ${data.phone} and your EmailId is ${data.email}.And here is want you want to say ${data.message} `)



        setData((preValue) =>{
            return{

                fullname : "",
                phone : "",
                email : "",
                message : ""

            }
        })
    }



    

    return (
        <>
            <div className="my-5 contact-bg">
                <h1 className="text-center fw-bold">Contact us</h1>
                <div className="container contact_div">
                    <div className="row my-5">
                        <div className="col-md-6 col-10 mx-auto">
                            <form action="" onSubmit={formSubmit} >

                                <div class="mb-3">
                                    <label for="exampleFormControlInput1" className="form-label fw-bold">Full Name :</label>
                                    <input type="text" class="form-control" 
                                    id="exampleFormControlInput1" 
                                    name="fullname"
                                    value={data.fullname}
                                    onChange={InputEvent}
                                    placeholder="Enter Your Full Name" required />
                                </div>
                                <div class="mb-3">
                                    <label for="exampleFormControlInput1" className="form-label fw-bold">Phone :</label>
                                    <input type="tel" class="form-control"
                                     id="exampleFormControlInput1" 
                                     name="phone"
                                     value={data.phone}
                                     onChange={InputEvent}
                                     pattern="[0-9]{10}" 
                                     placeholder="0123456789" required/>
                                </div>
                                <div class="mb-3">
                                    <label for="exampleFormControlInput1" className="form-label fw-bold">E-mail :</label>
                                    <input type="email" class="form-control"
                                     id="exampleFormControlInput1" 
                                     name="email"
                                     value={data.email}
                                     onChange={InputEvent}
                                     pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$" 
                                     placeholder="Something@gmail.com" required/>
                                </div>
                                <div className="mb-3">
                                    <label for="exampleFormControlTextarea1" class="form-label fw-bold">Message :</label>
                                    <textarea class="form-control" 
                                    id="exampleFormControlTextarea1" rows="3" 
                                    name="message"
                                    value={data.message}
                                    onChange={InputEvent}
                                    placeholder="Leave Your Message Here...." required></textarea>
                                </div>

                                <div className="col-12">
                                    <button class="btn btn-outline-primary btn-submit" type="submit">Get In Touch</button>
                                </div>


                            </form>

            


                        </div>

                    </div>

                   

                   
                </div>

                            
                              
                             

           
                       
               


            </div>

            <div className="container-fluid">
                <div className="row my-5">
                    <div className="col-md-6 col-10 mx-auto">
                            <h1 className="text-center fw-bold">Visit Us</h1>
                            <div className="d-flex justify-content-center align-items-center contact-Img">

                                    
                                    <img src={email} alt="visit-Img" width="50%" height="40%" />
                                 

                                    
                                    <img src={fb} alt="visit-Img" width="50%" height="40%" />
                                    

                                   
                                    <img src={insta} alt="visit-Img" width="50%" height="40%" />
                                   

                            </div>
                           
                    </div>

                </div>

            </div>
        </>
    )
}


export default Contact;




